import React, {Component}  from "react";

export default class Sample extends Component{
	state = {
		a:'Hello'
	};

	handleButtonClick = () =>
	{
		 this.setState( {a : "Murali"});
	}
	
	render(){
		return(
		<div>
			<h1>{this.state.a}</h1>
			<button type="button" onClick={this.handleButtonClick}>Click Me</button>
		</div>
		
		)
	}
}